import { Inject, Injectable } from '@angular/core';
import { SESSION_STORAGE, StorageService, isStorageAvailable } from 'angular-webstorage-service';
import { ROUTER_CONFIGURATION } from '@angular/router';

const STORAGE_KEY = 'app-questions';

@Injectable()
export class SessionService {

  constructor(@Inject(SESSION_STORAGE) private storage: StorageService) {}

  public sessionData(key: string, data: string[] = null): string[] {
    let dataKey = STORAGE_KEY + key;

    if (data !== null && this.storage.get(dataKey) === null) {
      this.storage.set(dataKey, data);
    }
    
    return this.storage.get(dataKey);
  }

  public sessionDataRemove(key: string): boolean {
    let dataKey = STORAGE_KEY + key;
    
    this.storage.remove(dataKey);

    return this.storage.get(dataKey) === null ? true:false;
  }
}



