import {Component, ElementRef, Inject, Output, EventEmitter, AfterViewInit, ChangeDetectorRef, Input} from '@angular/core';

declare var $: any;

@Component({
    selector: 'rang-slider',
    template: 
    `<div class="slider">
      <div id="slider"></div>
      <input type="text" class="q-text" [(ngModel)]="sliderVal" name="rang_slider" disabled>
    </div>`,
    styleUrls: ['./slider.component.css']
})

export class Slider implements AfterViewInit {
  @Input() sliderVal: number;
  @Output() onValueChanged = new EventEmitter<number>();//passing a string value to parent

  elementRef: ElementRef;

  constructor(@Inject(ElementRef) elementRef: ElementRef, public cdr: ChangeDetectorRef) {
    this.elementRef = elementRef;
  }

  ngAfterViewInit() { 
    $(this.elementRef.nativeElement).find('#slider').empty().slider({
      orientation: "vertical",
      range: "min",
      min: 0,
      max: 10,
      value: this.sliderVal,
      slide: ( event, ui ) => {
        this.sliderVal = ui.value;
        this.onValueChanged.emit(ui.value);
      },
      step: 0.01
    });
  }    
}


