import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { Question } from '../models/question.model';
import { ApiService } from './../services/api.service';
import { SessionService } from '../services/session.service';

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.css']
})
export class QuestionsComponent implements OnInit {
  title: string = "Rate Your Skills";
  userKey: string = 'user-form';
  message: string = '';
  questions: Array<Question>;
  
  constructor(
    private router: Router,
    private apiService: ApiService,
    private sessionService: SessionService
  ) { }

  ngOnInit(): void {
    let userSession = this.sessionService.sessionData(this.userKey);

    if (userSession == null) {
      this.router.navigate(['user']);
    }
    
    this.getQuestion();
  }

  getQuestion(): void {
    this.apiService.getQuestions()
      .then(questions => this.questions = questions );    
  }

  changeSliderVal(event, id:number) {
    this.questions.forEach(element => {
      if (element.id == id) {
        element.default_answer = event
      }
    });
  }
  
  saveAns(questionsForm: NgForm): void {
    let userData = this.sessionService.sessionData(this.userKey);
    
    if (userData == null) {
      this.router.navigate(['user']);
    }

    this.apiService.saveAnswers([{
      user: userData, 
      questions: questionsForm.value
    }]).then(res => {
      this.sessionService.sessionDataRemove(this.userKey);

      if (res.message == "success") {
        this.sessionService.sessionData('user_answers', res.data);

        this.router.navigate(['result']);
      } 

      this.message = res.message;
    });
  }

}



